*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@keyframes overlay {
  0% {
    top: 440px;
  }
  100% {
    top: 0;
  }
}
@keyframes reverse-overlay {
  0% {
    top: 0;
  }
  100% {
    top: 440px;
  }
}
